




























import router from '@/router';
import { Component, Prop, Vue } from 'vue-property-decorator';
import axios from 'axios';

console.log('hi', document.cookie);

@Component
export default class Authorize extends Vue {
    
    public isSign: boolean = true;
    public login = '';
    public password = '';
    protected go(){
        this.$store.dispatch('setLogin', this.login);
        this.$store.dispatch('setPassword', this.password);
        console.log(this.login, this.password)
        if (this.isSign){
          axios.post('http://192.168.193.19:8855/auth/users/', JSON.stringify({email: this.login, password: this.password}))
          .then((data) => {
            this.$store.dispatch('setToken', data.data.auth_token)
            console.log(data.data.auth_token)
            this.$router.push('/dashboard')
          });
        }
        else{
          axios.post('http://192.168.193.19:8855/auth/users/login/', JSON.stringify({email: this.login, password: this.password}))
          .then((data) => {
            this.$store.dispatch('setToken', data.data.auth_token)
            console.log(data.data.auth_token)
            this.$router.push('/dashboard')
          });
        }
        

    }
}
